<template>
  <div id="login">
    <label class="lab">
      <span class="demo-input-label">Username</span>
      <el-input v-model="username" placeholder="Please input" clearable />
    </label>
    <label class="lab">
      <span class="demo-input-label">Password</span>
      <el-input v-model="psw" placeholder="Please input password" show-password @keyup.enter="login"/>
    </label>
    <br />
    <div id="btn">
      <el-button type="success" @click="login">Login</el-button>
    </div>
  </div>
</template>

<script>
import { ref } from "vue";
import { useRouter } from "vue-router";
import axios from "axios";
import { ElNotification } from "element-plus";

export default {
  setup() {
    let username = ref(); // 绑定 username
    let psw = ref(); // 绑定 密码
    let isUsername = ref(false);
    let isPsw = ref(false);

    const router = useRouter();

    const login = () => {
      if (!username.value || !psw.value) {
        // alert("输入不能为空");
        ElNotification({
          title: "Warning",
          message: "用户名和密码不能为空!",
          type: "warning",
        });
        return;
      }

      axios({
        method: "post",
        url: "http://yangyixuan.com.cn/api/user/login",
        data: { username: username.value, password: psw.value },
      }).then((res) => {
        console.log(res);
        if (res.data.status === "0") {
          localStorage.setItem("token", res.data.token);
          router.push("/");
          ElNotification({
            title: "Success",
            message: "Login successfully",
            type: "success",
          });
        } else {
          // alert(res.data.message);
          ElNotification({
            title: "Error",
            message: res.data.message,
            type: "error",
          });
        }
      });
    };
    return {
      username,
      psw,
      login,
      isUsername,
      isPsw
    };
  },
};
</script>

